import React from 'react'
import {
    Row,
    Button
  } from "shards-react";
import * as jsPDF from 'jspdf'


function MakePDF(data){
/* HEADER */
  let doc = new jsPDF({orientation:'p', unit:'mm',format:'a4'});
  let imgData = 'data:image/png;base64,'
  doc.setFontSize(12)
  doc.setFont('times')
  doc.text(30,18,'Vpisna številka:')
  doc.setFontSize(16)
  doc.setFont('times','bold')
  doc.text(30,25,'36849/20')
  doc.setFontSize(12)
  doc.setFont('times','')
  doc.text(30,30,'Ime in priimek:')
  doc.text(60,30,(data.ime+' '+ data.priimek))
  doc.addImage(imgData,110,13,79,17)
  doc.line(30,35,190,35)


/* BODY */
doc.setFontSize(12)
doc.setFont('times','bold')
doc.text(30,40,'Anamneza:')
doc.setFont('times','')
doc.text(30,45,'Poškodovan kot motorist po trku z osebnim avtomobiloml. Pred poškodbo je bil ')
doc.text(30,50,data.mobilnost+'.')
doc.text(30,100,'Ime in priimek, dr. med.')

/*Naredi PDF */
doc.save(data.ime+'-'+data.priimek+'.pdf');
}



function FormToData(props) {
    return (
        <div>
          {MakePDF(props.data) /*Kliče funkcijo MakePDF */}
          {props.callback() /* kliče funkcijo v parent component (FormaAnamneza), da resertira count - potrdi da smo pritisnili submit prišli v ta component in z generirali formo */}
        </div>
    )
}

export default FormToData
